// onemap styles

.map-container {
	position: relative;
	max-width: 1140px;
	width: 95%;
	margin: 10px auto;
}

#mapdiv {
	position: relative;
	height: 650px;
}

.search-container {
	position: absolute;
	z-index: 1000;
	width: 320px;
	top: 0;
	margin: 10px 15px;
	padding: 20px;
	border: 1px solid #545;
	background: #fff;
	// opacity: 0.7;
}

.leaflet-routing-remove-waypoint {
	display: none;
}

.leaflet-top {
	left: 0;
}

.testing {
	color: red;
}

.active {
	background: #525ba3 !important;
	color: #fff;
}

.remove-start-waypoint,
.remove-end-waypoint {
	position: relative;
	background: #fff;
	display: inline-block;
	vertical-align: middle;
	cursor: pointer;
}

.leaflet-edit-marker-selected {
	background: none !important;
	border: 0 !important;
}

.remove-start-waypoint:after,
.remove-end-waypoint:after {
	content: "\00d7";
	position: absolute;
	display: block;
	width: 15px;
	height: 1px;
	z-index: 1;
	right: 1px;
	top: 4px;
	bottom: 0;
	margin: auto;
	padding: 2px;
	font-size: 18px;
	font-weight: bold;
	text-align: center;
	cursor: pointer;
	color: rgb(204,204, 204);
	background: #fff;
	margin-top: -16px;
	padding-right: 4px;
	line-height: 1px;
}